import React from 'react'
import styled from 'styled-components';
import {InnerLayout} from '../styles/Layouts';
import Title from '../Components/Title';
import ReviewItem from '../Components/ReviewItem';

function ReviewsSetion() {
    return (
        <ReviewsStyled>
            <Title title={'Reviews'} span={'Reviews'} />
            <InnerLayout>
                <div className="reviews">
                     <ReviewItem 
                        text={'Steven is well equipped to grow from challenges that he is presented with. His co-operative, persevering and hard-working attitude prepares him well for an internship. I strongly endorse Steven as I have every confidence that he will be great in whichever field in engineering he decides to pursue. - Dean of Mathematics, Anil Rawat'} 
                    />
                    <ReviewItem 
                        text={'Weinian is passionate in the electronic and computer related fields.With his strong goalsetting, he tends to gain first-hand practical experiencesas much as possible in the extra-curricular time.He is an impressive student with a very promising future. - Jing Tang, D.Eng.'} 
                    />
                   
                    
                </div>
            </InnerLayout>
        </ReviewsStyled>
    )
}

const ReviewsStyled = styled.section`
    .reviews{
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        grid-gap: 1.5rem;
        width: 100%;
        @media screen and (max-width:650px){
            grid-template-columns: repeat(1, 1fr);
        }
    }
`;

export default ReviewsSetion;
