import React from 'react';
import styled from 'styled-components';
import {InnerLayout} from '../styles/Layouts';
import Title from '../Components/Title';
import ServiceCard from '../Components/ServiceCard';


import data from '../img/data-analytics (1).png';
import ux from '../img/ux (1).png';
import ai from '../img/chip.png'


function ServicesSection() {
    return (
        <InnerLayout>
            <ServicesSectionStyled>
                <Title title={'Services'} span={'services'} />
                <div className="services">
                    <ServiceCard 
                        image={ux} 
                        title={'Web design and development'} 
                        paragraph={'My Startup forced me to try and do everything myself. Now I am continually learning the MERN Stack and able to develop the front-end of our first prototype. I might have little experience on Web Design/development, but having to build the whole website for my startup will enable me to understand every aspect of how to create a website from scratch.'}
                    />
                    <div className="mid-card">
                        <ServiceCard 
                            image={data} 
                            title={'Big data analytics'} 
                            paragraph={'Sky is not the limit, I always encourage myself to learn something new. This year, pushing myself into the data field, allows me to understand different aspects of big data analytics and do projects on actual datasets.'}
                        />
                    </div>
                    <ServiceCard 
                        image={ai} 
                        title={'Artificial Intelligence'} 
                        paragraph={'Majoring in AI allows me to develop, program and train the complex networks of algorithms that make up AI so that they can function like a human brain. Lastly also give me combined expertise in software development, programming, data science and data engineering.'}
                    />
                </div>
            </ServicesSectionStyled>
        </InnerLayout>
    )
}





const ServicesSectionStyled = styled.section`
    .services{
        margin-top: 5rem;
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        grid-gap: 1.5rem;
        @media screen and (max-width:1000px){
            flex-direction: column;
        }
        @media screen and (max-width:950px){
            grid-template-columns: repeat(2, 1fr);
        }
        @media screen and (max-width:650px){
            grid-template-columns: repeat(1, 1fr);
        }
       
    }
`;

export default ServicesSection;
