import React from 'react'
import styled from 'styled-components';
import resume from '../img/resume.jpg';
import PrimaryButton from './PrimaryButton';

function ImageSection() {
    return (
        <ImageSectionStyled>
            <div className="left-content">
                <img src={resume} alt=""/>
            </div>
            <div className="right-content">
                <h4>I am <span>Weinian Feng</span></h4>
                
                <p className="paragraph">
Raised in a small town by my grandfather in China I had no access to the internet. I was intrigued by a simple mp3 player or the occasional phone calls. The lack of technology never restricted my curious mind from imagining the functioning mechanics upon which everything is based. When primary 4, My parents sent me to Bangkok, giving me the opportunity to broaden my knowledge in various subject areas. Came to Thailand and spoke neither English or Thai, even basic communication became a barrier for me. To a point I almost gave up, but I realized that some of life’s best treasures are discovered only in challenging situations.

        
                </p>

                 <p className="paragraph">
From nothing to becoming a scholar student majoring in artificial intelligence, I didn’t come this far to only this far, I continue stepping out of my cultural tunnel and challenge myself even further in different fields. While leading multi tech related startup projects, I constantly join business cases to test my limiteds.I devote myself to creating the infrastructure destined to pave the path of our futures. I am ready to become a leader of this new, innovative era. A sanctuary where state-of-the-art internet services are available to all.


        
                </p>
                <div className="about-info">
                    <div className="info-title">
                        <p>Full Name</p>
                        <p>Age</p>
                        <p>Nationality </p>
                        <p>Languages </p>
                        <p>Location</p>
                        <p>Major</p>
                    </div>
                    <div className="info">
                        <p>: Weinian Feng</p>
                        <p>: 19</p>
                        <p>: Chinese </p>
                        <p>: Chinese, Thai, English </p>
                        <p>: Bangkok Thailand</p>
                        <p>: AI and Robotic engineering</p>
                    </div>
                </div>
                {/* <PrimaryButton title={'Download Cv'} /> */}
            </div>
        </ImageSectionStyled>
    )
}


const ImageSectionStyled = styled.div`
    margin-top: 5rem;
    display: flex;
    @media screen and (max-width:1000px){
        flex-direction: column;
        .left-content{
            margin-bottom: 2rem;
        }
    }
    .left-content{
        width: 100%;
        img{
            width: 95%;
            object-fit: cover;
        }
    }
    .right-content{
        width: 100%;
        h4{
            font-size: 2rem;
            color: var(--white-color);
            span{
                font-size: 2rem;
            }
        }
        .paragraph{
            padding: 1rem 0;
        }
        .about-info{
            display: flex;
            padding-bottom: 1.4rem;
            .info-title{
                padding-right: 3rem;
                p{
                    font-weight: 600;
                }
            }
            .info-title, .info{
                p{
                    padding: .3rem 0;
                }
            }
        }
    }
`;
export default ImageSection;
