import React from 'react';
import styled from 'styled-components';
import {InnerLayout} from '../styles/Layouts';
import Title from '../Components/Title';
import SmallTitle from '../Components/SmallTitle';
import BusinessCenterIcon from '@material-ui/icons/BusinessCenter';
import SchoolIcon from '@material-ui/icons/School';
import ThumbUpIcon from '@material-ui/icons/ThumbUp';
import ResumeItem from '../Components/ResumeItem';

function Resume() {
    const briefcase = <BusinessCenterIcon />
    const school = <SchoolIcon />
    const thumb = <ThumbUpIcon/>
    return (
        <ResumeStyled>
            <Title title={'Resume'} span={'resume'} />
            <InnerLayout>
                
                <div className="small-title">
                    <SmallTitle icon={briefcase} title={'Working Experince'} />
                </div>
                    <div className="resume-content">
                      <ResumeItem 
                        year={'Jan 2021 - Present'} 
                        title={'Co-founder and Chief Technology Officer'}
                        subTitle={'Gather - Scheduling Platform'}
                        text={'Gather is a Startup founded by group of Chulalongkorn students, We aims to build a platform for more convenient group activities/ meetings scheduling.  My role is mainly managed website building and designing and system structure building '} 
                    /> 
                    <ResumeItem 
                        year={'Sep 2021 - Present'} 
                        title={' Product Innovation'}
                        subTitle={'AIESEC in Thailand'}
                        text={' I am responsible for creation and subsequent introduction of a new good/service from scratch, to implementation.  Additionally, I am creating product for  AIESEC in Thailand, so, our products aim to create positive externalities to the society.'} 
                    />
                    <ResumeItem 
                        year={'May 2021 - Oct 2021'} 
                        title={'Marketing Specialist and Case Judge '}
                        subTitle={'YGGNITE Case Competition (AIESEC in Thailand)'}
                        text={"For marketing, I have to analyzing trends to help define the YGGNITE marketing strategy, and providing advice to reach YGGNITE's target market. As a Judge I am responsible for selection of qualifying teams (from 147 teams to 30 teams) "} 
                    />
                    <ResumeItem 
                        year={'May 2021 - August 2021 '} 
                        title={'Research Intern'}
                        subTitle={'Photonics Lab (Mahidol University)'}
                        text={' Research about digital holography microscopy and experiment with lasers in inline and off-axis holography. Build different Matlab codes to optimize and analyze the results to form the best image that can be used for further purpose. '} 
                    />
                      <ResumeItem 
                        year={'Aug 2020 - June 2021'} 
                        title={'Public Relation'}
                        subTitle={'F1 Rapidamente Chulalongkorn'}
                        text={'Firstly help to connect and persuade more companies to sponser F1 Chulalongkorn. Secondly representing F1 Chulalongkorn in local tournaments and presenting Chulalongkorn F1 project/models to invester in international events. '} 
                    />
                    <ResumeItem 
                        year={'June 2019 - July 2019'} 
                        title={'Research Intern'}
                        subTitle={'Applied DB public company limited'}
                        text={'As a research intern, my role is to research and experiment with different Polypropylene combinations, and test under different conditions. In the end, conclude and present on how different combination can used in different areas/fields'} 
                    />       
             <ResumeItem 
                        year={'June 2018 - July 2018'} 
                        title={'Engineer Intern '}
                        subTitle={'Thonburi Automotive Assembly Plant Co., Ltd.'}
                        text={'My First Intern! Most of the time, I spent time to learn how such a big company functions. But I also help on manage a well-coordinated and efficient logistic system between Mercedes Benz (Germany) to Assembly line (Thonburi)'} 
                    />

        

                </div>
                <div className="small-title u-small-title-margin">
                    <SmallTitle icon={thumb} title={'Achievement'} />
                </div> <div className="resume-content">
           <ResumeItem 
                        year={'2021'} 
                        title={'Finalist/Representative of Chulalongkorn '}
                        subTitle={'China-ASEAN Smart City Startup Competition'}
                        text={"Qualifying and representing my university in such a big international competition is really something new to me, even though I didn't win in the end, but I appreciate all my and my team’s hard work."} 
                    /> 
                    <ResumeItem 
                        year={'2020'} 
                        title={'Academic scholarship'}
                        subTitle={'Chulalongkorn University (ISE)'}
                        text={"What I learnt in Sixth-form wasn't ISE's requirement , so I had to learn everything I needed for ISE application in 2 months, but I still managed to receive a scholarship for my academic results."} 
                    />
                  
                    <ResumeItem 
                        year={'2019'} 
                        title={"International Award - Silver"}
                        subTitle={"The Duke of Edinburgh's Award"}
                        text={"It took me 10 months to complete the whole silver award, in 4 sections, Volunteering, Physical, Skills and Expedition. But It let me gain so many essential skills and attributes for work and life."} 
                    />
                    <ResumeItem 
                        year={'2018 '} 
                        title={'Academic scholarship'}
                        subTitle={'Singapore International School of Bangkok'}
                        text={"From can't even communicate with my classmates in Primary 4 to receiving an academic scholarship in secondary, I put in thousands of hours of hard work and always push myself to learn everyday."} 
                    />
               
                
                </div>
                 
                
                <div className="small-title u-small-title-margin">
                    <SmallTitle icon={school} title={'Educational Backgrounds'} />
                </div>
           
                   <div className="resume-content">
                    <ResumeItem 
                        year={'2020 - 2024'} 
                        title={'Robotic and Artificial Intelligence Engineering'}
                        subTitle={'Chulalongkorn University'}
                        text={'Robotics and AI engineering is an interdisciplinary engineering field that combines principles of mechanical, electrical, computer, system, and mechatronics engineering. Majoring this enable me to have knowledge of machinery design and computer.'} 
                    />
                    <ResumeItem 
                        year={'2018-2020'} 
                        title={'Cambridge A-Level'}
                        subTitle={'Singapore International School of Bangkok'}
                        text={'Cambridge International AS and A Levels are internationally benchmarked qualifications providing excellent preparation for university education. For me, I took Chemistry, Physics, Economics and Mathematic as my core subject to study in Sixth-form. '} 
                    />
                    <ResumeItem 
                        year={'2013 - 2018'} 
                        title={'IGCSE'}
                        subTitle={'Singapore International School of Bangkok'}
                        text={'IGCSE stands for “International General Certificate of Secondary Education”. It is equivalent to the GCSE qualification which is taken by students in Year 10 – 11 in the UK to prepare them for further pre-university level study, such as A-levels. '} 
                    />
                     <ResumeItem 
                        year={'2011 - 2013'} 
                        title={'PSHE'}
                        subTitle={'Natawan International School'}
                        text={"The PSLE assesses students' attainment of the learning outcomes of 4 subjects – English, Mathematics, Science and Mother Tongue. This assessment also facilitates the transition into the next phase of my education journey – secondary education."} 
                    />
                    <ResumeItem 
                        year={'Before 2011'} 
                        title={'义务教育'}
                        subTitle={'Chinese government school'}
                        text={'My basic education in China includes pre-school education (usually three years), primary education - six years, starting at the age of six, but due to family issue, I have to leave china at primary to purpue further education in Bangkok, Thailand. '} 
                    />
               
                
                </div>
                
            </InnerLayout>
        </ResumeStyled>    
    )
}

const ResumeStyled = styled.section`
    .small-title{
        padding-bottom: 3rem;
    }
    .u-small-title-margin{
        margin-top: 4rem;
    }

    .resume-content{
        border-left: 2px solid var(--border-color);
    }
`;
export default Resume
